* {
  box-sizing: border-box;
}

html, body {
  background-color: #030407;
  line-height: 1.5;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 18px;
}

.topnav {
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  z-index: 10;
  background: linear-gradient(#111a2a, #0e1625dd);
  align-items: center;
  gap: 32px;
  width: 100%;
  height: 72px;
  padding: 10px 32px;
  font-size: 24px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.topnav > :first-child {
  align-items: center;
  margin-right: auto;
  display: flex;
}

.topnav > :first-child img {
  margin-right: 12px;
}

@media (width <= 720px) {
  .topnav {
    padding: 12px 18px;
  }
}

label#sidenav-toggle-btn {
  z-index: 1000;
  flex-direction: column;
  justify-content: space-between;
  width: 35px;
  height: 35px;
  padding: 8px 6px;
  display: flex;
  position: fixed;
  top: 18.5px;
  right: 18px;
  transform: translateZ(1px);
}

label#sidenav-toggle-btn .line, label#sidenav-toggle-btn .center {
  z-index: 1;
  background-color: #fff;
  border: 1.5px solid #fff;
  border-radius: 50px;
  width: 100%;
  transition: transform 1s;
  position: relative;
}

label#sidenav-toggle-btn .center {
  z-index: 0;
  pointer-events: none;
  width: calc(100% - 12px);
  height: 0;
  transition: width 1s, height 1s, opacity 1s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.overlay {
  pointer-events: none;
  opacity: .5;
  z-index: 100;
  overscroll-behavior: contain;
  -webkit-tap-highlight-color: #0000;
  background-color: #0000;
  width: 100vw;
  height: 100vh;
  transition: background-color .5s;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
  transform: translateZ(0);
}

.overlay::-webkit-scrollbar {
  display: none;
}

.overlay::-moz-scrollbar {
  display: none;
}

.overlay label {
  position: absolute;
  inset: 0;
}

.sidenav {
  z-index: 100;
  text-transform: uppercase;
  letter-spacing: 1px;
  overscroll-behavior: contain;
  background-color: #030407;
  width: 320px;
  height: 100vh;
  margin: 0;
  padding: 72px 44px 48px;
  font-size: 20px;
  transition: right .75s;
  position: fixed;
  top: 0;
  right: -320px;
  overflow: auto;
}

.sidenav ul, .sidenav li {
  list-style-type: none;
}

.sidenav ul {
  margin: 0;
  padding-top: 6px;
  padding-left: 0;
}

.sidenav ul li {
  text-transform: none;
  letter-spacing: 0;
  font-size: 17px;
}

.sidenav li {
  padding: 6px 0;
}

#sidenav-toggle:checked ~ label#sidenav-toggle-btn .line:first-child {
  transform: translate(0, 8px)rotate(-45deg)scaleX(1.25);
}

#sidenav-toggle:checked ~ label#sidenav-toggle-btn .line:last-child {
  transform: translate(0, -8px)rotate(45deg)scaleX(1.25);
}

#sidenav-toggle:checked ~ label#sidenav-toggle-btn .center {
  opacity: .2;
  width: 44px;
  height: 44px;
}

#sidenav-toggle:checked ~ .sidenav {
  right: 0;
}

#sidenav-toggle:checked ~ .overlay {
  pointer-events: all;
  background-color: #444;
}

.topnav a, .sidenav a, footer a {
  color: inherit;
  text-decoration: none;
  transition: opacity .2s;
}

.topnav a:hover, .sidenav a:hover, footer a:hover {
  opacity: .8;
}

.topnav a:active, .sidenav a:active, footer a:active {
  opacity: .5;
}

.topnav a:not([href]), .sidenav a:not([href]), footer a:not([href]) {
  opacity: .3;
}

.center {
  text-align: center;
}

.mt-64 {
  margin-top: 64px;
}

.hug-wave {
  z-index: 1;
  margin-bottom: calc(-2vw - 60px);
  position: relative;
}

section {
  padding: 32px;
  position: relative;
  overflow-x: clip;
}

section.gradient {
  background-image: linear-gradient(#111a2b, #030407);
  background-repeat: no-repeat;
  background-size: 100% 480px;
  min-height: 480px;
}

section.fullsize {
  min-height: 840px;
}

@media (width <= 1100px) {
  section.fullsize {
    min-height: 640px;
  }
}

@media (width <= 720px) {
  section.fullsize {
    min-height: 0;
  }
}

section.page-header {
  padding: 100px 5vw 10vh;
}

section.page-header .heading-container {
  padding-inline: 32px;
}

section.page-header h1, section.page-header h2 {
  margin-block: 16px;
}

section.page-header.gradient {
  background-repeat: no-repeat;
  background-size: 100% 480px;
}

section a {
  color: inherit;
}

h1, h2, h3 {
  font-weight: inherit;
}

h1 {
  font-size: 40px;
}

h2 {
  font-size: 24px;
}

@media (width <= 720px) {
  h1 {
    font-size: 28px;
  }

  h2 {
    font-size: 20px;
  }

  body [data-screen="desktop-only"] {
    display: none !important;
  }
}

@media (width >= 720.1px) {
  body [data-screen="mobile-only"] {
    display: none !important;
  }
}

.content-only {
  display: contents;
}

.timeline {
  justify-content: center;
  gap: 32px;
  margin: auto;
  padding: 10vh 10% 0;
  display: flex;
}

@media (width <= 960px) and (width >= 720.1px) {
  .timeline {
    padding-left: 5%;
  }
}

.timeline.tl-start {
  padding-top: 20vh;
}

.timeline .heading-container {
  flex-direction: column;
  justify-content: space-around;
  height: 128px;
  padding-block: 6px 12px;
  display: flex;
}

.timeline .heading-container h1, .timeline .heading-container h2 {
  font-weight: inherit;
  margin-block: 0;
}

.timeline .content {
  z-index: 0;
  width: 100%;
  max-width: 840px;
}

.timeline .content h3 {
  font-size: 36px;
  font-weight: inherit;
  margin-bottom: 0;
}

.timeline .content p {
  font-size: 24px;
}

.timeline .content .large {
  font-size: 28px;
}

.timeline .content .heading-container, .timeline .content p {
  z-index: 1;
  position: relative;
}

.timeline .line {
  z-index: 1;
  grid-auto-rows: 128px;
  grid-auto-columns: 128px;
  place-items: center;
  width: 128px;
  display: grid;
  position: relative;
}

.timeline .line:after {
  content: " ";
  background-color: #fff;
  width: 10px;
  height: calc(10vh + 100%);
  position: absolute;
  top: 64px;
  left: 59px;
}

.timeline.tl-start .line:after {
  background-color: #0000;
  background-image: linear-gradient(#fff6, #fff 60%);
  height: calc(20vh + 100%);
}

.timeline .img-container {
  z-index: 1;
  position: relative;
}

.timeline:not(.tl-start) .line .img-container {
  --test: 1;
  width: calc(var(--test, 1) * 96px);
  height: calc(var(--test, 1) * 96px);
  border: 4px solid #fff;
  border-width: calc(15px - var(--test, 1) * 10px);
  background-image: radial-gradient(50% 50%, #4b5982 0%, #0f172e 99.99%);
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  margin: 0;
  transition: all 1s;
  display: flex;
  overflow: hidden;
}

.timeline.tl-start, .timeline.tl-end {
  padding-bottom: 10vh;
}

.timeline.tl-end .heading-container {
  padding-top: 32px;
}

.timeline.tl-end .heading-container h1 {
  margin-bottom: 12px;
}

.timeline.tl-end .line:after {
  display: none;
}

.timeline.tl-end .line .img-container {
  --test: 0;
}

.timeline.tl-end .heading-container {
  height: auto;
}

@media (width <= 720px) {
  .timeline {
    flex-direction: column;
    gap: 0;
    position: relative;
  }

  .timeline .line {
    grid-auto-rows: unset;
    width: 100%;
    position: relative;
    top: -2vh;
    left: 0;
  }

  .timeline .line:after {
    width: 150%;
    height: 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .timeline .line .img-container {
    transform: translateX(-20px);
  }

  .timeline .heading-container {
    gap: 4px;
    height: auto;
  }

  .timeline .content .large {
    font-size: 22px;
  }

  .timeline.tl-start {
    padding-bottom: 0;
  }

  .timeline.tl-start .img-container img {
    width: 96px;
    height: auto;
  }

  .timeline.tl-start .line:after {
    display: none;
  }

  .timeline.tl-end {
    padding-top: 5vh;
  }

  .timeline.tl-end .line {
    display: none;
  }

  .timeline .language-grid {
    grid-template-columns: repeat(auto-fill, 50px);
    grid-auto-rows: 50px;
    grid-auto-flow: row;
    gap: 25px;
    max-width: 425px;
    display: grid;
  }

  .timeline .content h3 {
    font-size: 28px;
  }

  .timeline .content p {
    font-size: 20px;
  }

  .timeline .showcase-item img {
    z-index: 1;
    max-width: 100%;
    height: auto;
    position: relative;
  }
}

