@keyframes slide-up {
  0% {
    opacity: .1;
    transform: translateY(8%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.language-grid {
  grid-template-columns: repeat(6, 84px);
  grid-auto-rows: 84px;
  grid-auto-flow: row;
  gap: 42px;
  display: grid;
}

.language-grid img {
  width: 100%;
  height: auto;
  transition: opacity .2s, transform .2s;
  position: absolute;
}

.language-grid label {
  position: relative;
}

.language-grid label .color, .language-grid label input:checked ~ .monochrome {
  opacity: 0;
}

@media (width <= 1080px) {
  .language-grid {
    grid-template-columns: repeat(4, 72px);
    grid-auto-rows: 72px;
  }
}

.langdesc {
  display: none;
}

.showcase-item {
  z-index: 0;
  position: relative;
}

.showcase-item .img-container {
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  position: relative;
}

.showcase-item .img-container:before {
  content: " ";
  pointer-events: none;
  background-image: radial-gradient(50% 50% at 50% 50%, var(--color, transparent) 30%, transparent 100%);
  z-index: -1;
  border-radius: 50%;
  width: 100vw;
  max-width: 1400px;
  height: 65vw;
  max-height: 800px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.showcase-item .img-container img {
  z-index: 1;
  width: 100%;
  max-width: 706px;
  height: auto;
  position: relative;
}

.showcase-item p.links {
  gap: 16px;
  margin-block: 16px;
  font-size: 18px;
  display: flex;
}

.showcase-item p.links a {
  color: #fff;
  font-size: 18px;
  line-height: 1.25;
  text-decoration: none;
  transition: opacity .3s;
  position: relative;
}

.showcase-item p.links a:hover {
  opacity: .9;
}

.showcase-item p.links a:active {
  opacity: .6;
}

.showcase-item p.links a:before {
  content: "";
  opacity: .25;
  background-color: #fff;
  width: 0%;
  transition: width .3s, opacity .3s;
  display: block;
  position: absolute;
  inset: 100% 0 -2px;
}

.showcase-item p.links a:after {
  content: "";
  background-image: url("images/arrow-diagonal.svg");
  background-position: 100% 100%;
  background-repeat: no-repeat;
  background-size: 16px;
  width: 20px;
  height: 20px;
  transition: transform .3s;
  display: inline-block;
  position: relative;
  top: 1px;
}

.showcase-item p.links a:hover:before {
  opacity: 1;
  width: 102%;
}

.showcase-item p.links a:hover:after {
  transform: scale(1.2)translate(1px, -1px);
}

.project-grid {
  grid-template-columns: repeat(auto-fill, min(440px, 100%));
  grid-auto-flow: row;
  justify-content: center;
  gap: 48px;
  max-width: 100%;
  display: grid;
}

.project-grid .project-card {
  color: inherit;
  -webkit-text-decoration: inherit;
  text-decoration: inherit;
  transform-style: preserve-3d;
  background: #2b2d32;
  border-radius: 8px;
  width: 440px;
  max-width: 100%;
  transition: opacity .2s, box-shadow .2s;
}

.project-grid .project-card h3, .project-grid .project-card p {
  margin-inline: 18px;
  transform: translateZ(12px);
}

.project-grid .project-card h3 {
  margin-block: 14px 8px;
  font-size: 25px;
  font-weight: bold;
}

.project-grid .project-card p {
  margin-block: 0 18px;
  font-size: 17px;
}

.project-grid .project-card p.subtitle {
  margin-block: -8px 8px;
}

@media (width <= 720px) {
  .project-grid .project-card h3 {
    font-size: 20px;
  }
}

.project-grid .project-card > img {
  width: inherit;
  border-radius: 8px 8px 0 0;
  max-width: 100%;
  height: auto;
  transition: box-shadow .2s;
  display: flex;
  position: relative;
}

.project-grid .project-card:hover {
  box-shadow: 0 0 32px #fff2;
}

.project-grid .project-card:hover * {
  z-index: 1;
  position: relative;
}

.project-grid .project-card:hover:before {
  content: " ";
  width: 115%;
  height: 115%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.project-grid .project-card:hover > img {
  box-shadow: 0 4px 12px 2px #1112;
}

.project-grid .project-card:active {
  opacity: .8;
}

.footer-lead-in {
  width: 100%;
  position: absolute;
  bottom: min(160px - 30vw, -40px);
  left: 0;
}

footer {
  align-items: center;
  height: calc(33vw + 48px);
  max-height: 512px;
  padding: 8vw 10% 48px;
  display: flex;
  position: relative;
  overflow: hidden;
}

footer:after {
  content: " ";
  background-color: #172444;
  position: absolute;
  inset: 30vw 0 0;
}

footer > img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

footer > img ~ * {
  z-index: 1;
  position: relative;
}

footer .footer-flex {
  gap: 50px;
  width: 100%;
  display: flex;
}

footer .footer-flex :first-child {
  margin-right: auto;
}

footer .footer-flex h3 {
  margin-block: 0 18px;
  font-size: 24px;
}

footer .footer-flex ul {
  padding-left: 0;
  list-style: none;
}

footer .footer-flex li {
  margin-block: 12px;
}

footer .footer-flex .column li * > a {
  text-decoration: underline;
}

@media (width <= 1140px) {
  footer {
    padding-inline: 5%;
  }

  footer .footer-flex {
    gap: 32px;
  }
}

@media (width <= 960px) {
  footer {
    padding-top: calc(8vw + 36px);
    height: auto;
    max-height: none;
    padding-inline: 10%;
  }

  footer .footer-flex {
    flex-direction: column;
    justify-content: flex-end;
    gap: 16px;
    max-width: 720px;
    margin-inline: auto;
  }

  footer .footer-flex li {
    margin-block: 6px;
  }

  footer .footer-flex .column:first-child {
    position: absolute;
    top: 0;
    right: 0;
  }
}

p.caption {
  margin-block: 0;
  font-size: 16px;
}

